import axios from "axios";
export default {
  data() {
    return {
      username: "",
      password: "",
      passwordAgain: ""
    };
  },
  methods: {
    logup() {
      if (this.password.length <= 4) {
        alert("密码长度必须大于四位");
      } else {
        if (this.password !== this.passwordAgain) {
          alert("两次输入的密码不一致");
        } else {
          var data = {
            username: this.username,
            password: this.password
          };
        //   console.log(data);
          axios.post("/logup", data).then(res => {
            if (res.data === "ok") {
              this.$router.push({path: '/'});
            }
          });
        }
      }
    }
  }
};



