import axios from "axios";
import CountDown from "vue2-countdown";
import { format } from "url";
import { setInterval, clearInterval } from "timers";
export default {
  components: {
    CountDown
  },
  data() {
    return {
      name,
      totalPeople: 999999,
      totalMoney: 999999,
      successPeople: 0,
      failPeople: 0,
      name1: "",
      name2: "",
      name3: "",
      hitCardTime: "",
      hitCardMoney: 0,
      hitCardCount: 0,
      btnText: "参与打卡挑战",
      startTime: new Date().getTime(),
      currentTime: new Date().getTime(),
      openRedBag: false,
      canJoin: false
    };
  },
  computed: {
    endTime() {
      var date = new Date();
      var month = date.getMonth() + 1;
      var nextDay = date.getDate() + 1;
      var today = date.getDate();
      var hour = date.getHours();
      if(hour>=8){
        var time = new Date("2018-" + month + "-" + nextDay + " 05:00:00").getTime();
        return time;
      }else{
        var time = new Date("2018-" + month + "-" + today + " 05:00:00").getTime();
        return time;
      }
    }
  },
  mounted: async function get() {
    this.name = this.$route.query.name;
    // console.log(this.$route.query.name);
    var date = new Date();
    var month = date.getMonth() + 1;
    var day = date.getDate();
    var data = {
      name: this.name
    };
    //这里用time_range方法判断签到时间是否在早八点到五点
    this.openRedBag = this.time_range("05:00", "08:00");
    //用time_range方法判断参与时间是否符合时间，
    this.canJoin = this.time_range("08:00", "23:50");
    var info = await new Promise((resolve, reject) => {
      axios.post("/getInfo", data).then(res => {
        resolve(res.data);
      });
    });
    this.totalPeople = info.todayJoinCount;
    this.totalMoney = info.todayJoinCount;
    this.successPeople = info.hitCardDocs.length;
    this.failPeople = info.yesterdayJoinDocs.length - info.hitCardDocs.length;
    console.log(info);

    // 开始写早起之星逻辑
    if (info.earlestStar != null) {
      var earlestStar = info.earlestStar;
      this.name1 = earlestStar.name;
      var earlestStarTime = earlestStar.createTime;
      var earlestStarTimeHour = earlestStarTime.getHours();
      var earlestStarTimeMinute = earlestStarTime.getMinutes();
      var earlestStarTimeStr = String(earlestStarTimeHour + ':' + earlestStarTimeMinute);
      this.hitCardTime = earlestStarTimeStr;
    }
    
    // 开始写毅力之星的逻辑
    if (info.harderStar != null) {
      var harderStar = info.harderStar;
      this.name3 = harderStar.name;
      this.hitCardCount = harderStar.money;
    }
    
    // 开始写运气之星的逻辑
    if(info.luckStar != null) {
      var luckStar = info.luckStar;
      this.name2 = luckStar.name;
      this.hitCardMoney = luckStar.getMoney;
    }
    var now = new Date();
    var hour = now.getHours();
    //判断时间在八点的前后
    if (hour >= 8) {
      if (info.todayHaveUser) {
        this.btnText = "签到";
      } else {
        this.btnText = "参与打卡挑战";
      }
    } else {
      if (info.yesterdayHaveUser) {
        this.btnText = "签到";
      } else {
        this.btnText = "参与打卡挑战";
      }
    }
  },
  methods: {
    countDownS_cb: function(x) {
      console.log("计时开始");
    },
    countDownE_cb: function(x) {
      console.log("计时结束");
    },
    join() {
      if (this.btnText === "参与打卡挑战" && this.canJoin) {
        alert("支付成功,参与挑战");
        var data = {
          info: {
            name: this.name,
            money: 1
          }
        };
        axios.post("/join", data).then(res => {
          // console.log(res);
          if ((res.data = "ok")) {
            window.location.reload();
          }
        });
      } else if (this.btnText === "签到" && this.openRedBag) {
        axios.post("/successHitCard", { name: this.name }).then(res => {
          if (res.data == "ok") {
            alert("签到成功,明天再接再厉!");
            this.btnText = "参与打卡挑战";
          } else {
            alert(res.data);
          }
        });
      } else {
        alert("时间还没到哦");
      }
    },
    time_range(beginTime, endTime) {
      var strb = beginTime.split(":");
      if (strb.length != 2) {
        return false;
      }

      var stre = endTime.split(":");
      if (stre.length != 2) {
        return false;
      }
      var b = new Date();
      var e = new Date();
      var n = new Date();
      b.setHours(strb[0]);
      b.setMinutes(strb[1]);
      e.setHours(stre[0]);
      e.setMinutes(stre[1]);
      if (n.getTime() - b.getTime() > 0 && n.getTime() - e.getTime() < 0) {
        return true;
      } else {
        return false;
      }
    },
    gotoMy() {
      this.$router.replace({path: '/My',query:{name: this.name}});
    }
  },
};



