import axios from "axios";
import { clearTimeout } from 'timers';
export default {
    data(){
        return{
            username:'',
            password:''

        }
    },
    methods: {
        login() {
            var data = {
                username: this.username,
                password: this.password
            }
            axios.post('/login',data).then(res =>{
                if(res.data === 'ok'){
                    alert('登录成功');
                    this.$router.push({path: '/Home',query:{name: this.username}});
                }else{
                    alert(res.data);

                }
            })
        }
    }
};



